import React, { useEffect } from "react";
import { Container } from "semantic-ui-react";
import NavBar from "./NavBar";
import ActivityDashboard from "../../features/activities/dashboard/ActivityDashboard";
import { observer } from "mobx-react-lite";
import { Route, Switch } from "react-router";
import HomePage from "../../features/home/HomePage";
import ActivityForm from "../../features/activities/forms/ActivityForm";
import ActivityDetails from "../../features/activities/details/ActivityDetails";
import { useLocation } from "react-router-dom";
import TestErrors from "../../features/errors/TestErrors";
import { ToastContainer } from "react-toastify";
import NotFound from "../../features/errors/NotFound";
import ServerError from "../../features/errors/ServerError";
import { useStore } from "../stores/store";
import LoadingComponent from "./LoadingComponent";
import ModalContainer from "../common/modals/ModalContainer";
import ProfilePage from "../../features/users/profiles/ProfilePage";
import PrivateRoute from "./PrivateRoute";

function App() {
	const location = useLocation();
	const { commonStore, userStore } = useStore();

	useEffect(() => {
		if (commonStore.token) {
			userStore.getUser().finally(() => commonStore.setAppLoaded());
		} else {
			// temp to allow local testing without fb https restrictions
			if (window.FB) {
				userStore.getFacebookLoginStatus().then(() => commonStore.setAppLoaded());
			} else {
				commonStore.setAppLoaded();
			}
		}
	}, [userStore, commonStore]);

	if (!commonStore.appLoaded) return <LoadingComponent content="Loading app..." />;
	return (
		<>
			<ToastContainer position="bottom-right" hideProgressBar></ToastContainer>
			<ModalContainer />
			<Route exact path="/" component={HomePage} />
			<Route
				path={"/(.+)"}
				render={() => (
					<>
						<NavBar />
						<Container style={{ marginTop: "8em" }}>
							<Switch>
								<PrivateRoute exact path="/activities" component={ActivityDashboard} />
								<PrivateRoute path="/activities/:id" component={ActivityDetails} />
								<PrivateRoute path="/profiles/:username" component={ProfilePage} />
								<PrivateRoute key={location.key} path={["/createActivity", "/manage/:id"]} component={ActivityForm} />
								<PrivateRoute path="/errors" component={TestErrors} />
								<Route path="/server-error" component={ServerError} />

								{/* default route if undefined route entered */}
								<Route component={NotFound} />
							</Switch>
						</Container>
					</>
				)}
			/>
		</>
	);
}

export default observer(App);
