import { format } from "date-fns";
import { makeAutoObservable, reaction, runInAction } from "mobx";
import agent from "../api/agent";
import { Filters } from "../common/enums/Filters";
import { Activity, ActivityFormValues } from "../models/activity";
import { Pagination, PagingParams } from "../models/pagination";
import { Profile } from "../models/profile";
import { store } from "./store";

export default class ActivityStore {
	activityRegistry = new Map<string, Activity>();
	selectedActivity: Activity | undefined = undefined;
	editMode = false;
	loading = false;
	loadingInitial = false;
	pagination: Pagination | null = null;
	pagingParams = new PagingParams();
	predicate = new Map().set(Filters.all, true);

	constructor() {
		makeAutoObservable(this);

		reaction(
			() => this.predicate.keys(),
			() => {
				// reset pagination to first page of result after applying new filter
				this.pagingParams = new PagingParams();
				this.activityRegistry.clear();
				this.loadActivities();
			}
		);
	}

	// computed properties
	get activitiesByDate() {
		return Array.from(this.activityRegistry.values()).sort((a, b) => a.date!.getTime() - b.date!.getTime());
	}

	get groupedActivities() {
		return Object.entries(
			this.activitiesByDate.reduce((activities, activity) => {
				const date = format(activity.date!, "dd MMM yyyy");
				activities[date] = activities[date] ? [...activities[date], activity] : [activity];
				return activities;
			}, {} as { [key: string]: Activity[] })
		);
	}

	get axiosParams() {
		const params = new URLSearchParams();
		params.append("pageNumber", this.pagingParams.pageNumber.toString());
		params.append("pageSize", this.pagingParams.pageSize.toString());

		this.predicate.forEach((value, key) => {
			if (key === Filters.startDate) {
				params.append(key, (value as Date).toISOString());
			} else {
				params.append(key, value);
			}
		});
		return params;
	}

	setPredicate = (predicate: string, value: string | Date) => {
		const resetPredicate = () => {
			this.predicate.forEach((value, key) => {
				if (key !== Filters.startDate) this.predicate.delete(key);
			});
		};

		switch (predicate) {
			case Filters.all:
				debugger;
				resetPredicate();
				this.predicate.set(Filters.all, value);
				break;
			case Filters.isGoing:
				resetPredicate();
				this.predicate.set(Filters.isGoing, value);
				break;
			case Filters.isHost:
				resetPredicate();
				this.predicate.set(Filters.isHost, value);
				break;
			case Filters.startDate:
				// we must first delete previous date to trigger change reaction
				this.predicate.delete(Filters.startDate);
				this.predicate.set(Filters.startDate, value);
				break;
			default:
				console.error("Predicate key unknown!");
				break;
		}
	};

	loadActivities = async () => {
		this.setLoadingInitial(true);
		try {
			const result = await agent.Activities.list(this.axiosParams);
			result.data.forEach((activity) => {
				this.setActivity(activity);
			});

			this.setPagination(result.pagination);
			this.setLoadingInitial(false);
		} catch (error) {
			console.log(error);
			this.setLoadingInitial(false);
		}
	};

	setPagination = (pagination: Pagination) => {
		this.pagination = pagination;
	};

	setPagingParams = (pagingParams: PagingParams) => {
		this.pagingParams = pagingParams;
	};

	loadActivity = async (id: string) => {
		let activity = this.getActivity(id);

		if (activity) {
			this.selectedActivity = activity;
			return activity;
		} else {
			this.loadingInitial = true;
			try {
				activity = await agent.Activities.details(id);
				this.setActivity(activity);
				runInAction(() => {
					this.selectedActivity = activity;
				});

				this.setLoadingInitial(false);
				return activity;
			} catch (error) {
				console.log(error);
				this.setLoadingInitial(false);
			}
		}
	};

	private getActivity = (id: string) => {
		return this.activityRegistry.get(id);
	};

	private setActivity = (activity: Activity) => {
		const user = store.userStore.user;

		if (user) {
			activity.isGoing = activity.attendees!.some((a) => a.username === user.username);
			activity.isHost = activity.hostUsername === user.username;
			activity.host = activity.attendees?.find((x) => x.username === activity.hostUsername);
		}

		activity.date = new Date(activity.date!);
		this.activityRegistry.set(activity.id, activity);
	};

	// this method was implemented to avoid the warning about strict mode (see runInAction in documentation)
	setLoadingInitial = (state: boolean) => {
		this.loadingInitial = state;
	};

	createActivity = async (activity: ActivityFormValues) => {
		const user = store.userStore.user;
		const attendee = new Profile(user!);
		const newActivity = new Activity(activity);
		newActivity.hostUsername = user!.username;
		newActivity.attendees = [attendee];
		this.setActivity(newActivity);
		try {
			await agent.Activities.create(activity);

			runInAction(() => {
				this.selectedActivity = new Activity();
			});
		} catch (error) {
			console.log(error);
		}
	};

	updateActivity = async (activity: ActivityFormValues) => {
		try {
			await agent.Activities.update(activity);
			runInAction(() => {
				if (activity.id) {
					let updatedActivity = { ...this.getActivity(activity.id), ...activity };
					this.activityRegistry.set(activity.id, updatedActivity as Activity);
					this.selectedActivity = updatedActivity as Activity;
				}
			});
		} catch (error) {
			console.log(error);
		}
	};

	deleteActivity = async (id: string) => {
		this.loading = true;
		try {
			await agent.Activities.delete(id);
			runInAction(() => {
				this.activityRegistry.delete(id);
				this.loading = false;
			});
		} catch (error) {
			console.log(error);
			runInAction(() => {
				this.loading = false;
			});
		}
	};

	updateAttendance = async () => {
		const user = store.userStore.user;
		this.loading = true;

		try {
			await agent.Activities.attend(this.selectedActivity!.id);

			runInAction(() => {
				if (this.selectedActivity?.isGoing) {
					this.selectedActivity.attendees = this.selectedActivity.attendees?.filter((u) => u.username !== user?.username);
					this.selectedActivity.isGoing = false;
				} else {
					const attendee = new Profile(user!);
					this.selectedActivity?.attendees?.push(attendee);
					this.selectedActivity!.isGoing = true;
				}

				this.activityRegistry.set(this.selectedActivity!.id, this.selectedActivity!);
			});
		} catch (error) {
			console.log(error);
		} finally {
			runInAction(() => (this.loading = false));
		}
	};

	cancelActivityToggle = async () => {
		this.loading = true;

		try {
			await agent.Activities.attend(this.selectedActivity!.id);
			runInAction(() => {
				this.selectedActivity!.isCancelled = !this.selectedActivity?.isCancelled;
				this.activityRegistry.set(this.selectedActivity!.id, this.selectedActivity!);
			});
		} catch (error) {
			console.log(error);
		} finally {
			this.loading = false;
		}
	};

	clearSelectedActivity = () => {
		this.selectedActivity = undefined;
	};

	updateAttendeeFollowing = (username: string) => {
		this.activityRegistry.forEach((activity) => {
			activity.attendees!.forEach((attendee) => {
				if (attendee.username === username) {
					attendee.following ? attendee.followersCount-- : attendee.followersCount++;
					attendee.following = !attendee.following;
				}
			});
		});
	};
}
